.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* ***************************   html ScrollBar *************************** */
html::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

html::-webkit-scrollbar-thumb {
  background-color: #e5ddd5;
}

html::-webkit-scrollbar-track {
  background: rgba(#aaaaaa, .1);
}

.right {
  border-left: 1px solid #eee;
  padding-top: 5px;
  padding-bottom: 5px;

}

.users {
  overflow-y: scroll;
  position: relative;
  /* height: 100vh; */
}

.conAdd {
  position: absolute;
  top: 110px;
  left: 10px;
  background-color: #fff;
  overflow: auto;
  overflow-x: hidden;
  height: 100vh;
  width: 440px;
  cursor: pointer;
  overflow-y: scroll;
}

.conAdd::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

.conAdd::-webkit-scrollbar-thumb {
  background-color: #f0eeec;
}

.conAdd::-webkit-scrollbar-track {
  background: rgba(#aaaaaa, .1);
}

/* ************** */
.users::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

.users::-webkit-scrollbar-thumb {
  background-color: #f0eeec;
}

.users::-webkit-scrollbar-track {
  background: rgba(#aaaaaa, .1);
}

/* ***************************  Right -side ************************** */

.main {
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
}



/* *********************************    Chat users area  ****************************************** */
.chat {
  background-color: #e5ddd5;
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/1089577/background.png);
  width: 100%;
  padding: 0px 5%;
  padding-top: 7px;
  overflow-y: scroll;
  height: 80vh;
}

.chat::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

.chat::-webkit-scrollbar-thumb {
  background-color: #f0eeec;
}

.chat::-webkit-scrollbar-track {
  background: rgba(#aaaaaa, .1);
}

/* *********************************    Chat Box  ****************************************** */
.chatbox {
  width: 100%;
  height: 60px;
}

.information {
  width: 100%;
  position: relative;
  background: #f7f7f7;
  display: none;
  flex-direction: column;
  overflow-y: scroll;
}

/* *************************************  Message Area  ******************************** */
.wrap-message {
  width: 100%;
  height: 60px;
  background: #f1f1f1;
  display: flex;
}

.input-message {
  width: 100%;
  margin: 0px 10px;
  border: none;
}

.wrap-message i {
  color: #aaaaaa;
  text-align: center;
}

.left-side {
  display: flex;
  flex-direction: column;
  height: 100%;

}

.uk {
  background-color: #fff;
  flex: none;
}

.way.way1 {
  padding-right: 0;
  border-bottom: 1px solid rgb(220, 219, 219);
}

.way {
  padding: 0 12px;
  background-color: #fff;
}

.way1 {
  box-sizing: border-box;
  display: flex;
  flex: none;
  align-items: center;
  height: 49px;
  transition: box-shadow .18s ease-out, background-color .25s ease-out;
}

.tk {
  position: relative;
  box-sizing: border-box;
  flex: 1;
  overflow: hidden;
}

.button {
  position: relative;
  box-sizing: border-box;
  flex: 1;
  overflow: hidden;
}

._1UWac._3hKpJ ._13NKt {
