/* ****************************  chatting information *******************************************/
.message-in {
  align-items: flex-start;
  margin-top: 10px;
}

.FMR {
  position: relative;
  display: flex;
  flex-direction: column;
  user-select: text;

}

msg-container {
  position: relative;
  flex: none;
  font-size: 14.2px;
  line-height: 19px;
  color: #aaaaaa;
  max-width: 85%;
}

.it {
  background-color: #fff;
  box-shadow: 0 1px 0.5px rgb(169, 170, 171);
  border-radius: 7.5px;
  position: relative;
  z-index: 200;

}

.msg-text {
  padding: 6px 7px 8px 9px;
  box-sizing: border-box;
}

.msgTime {
  float: right;
  margin: -10px 0 -5px 4px;
  position: relative;
  z-index: 10;
}

.message-out {
  align-items: flex-end;
  margin-top: 10px;
}

.its {
  background-color: rgb(202, 220, 247);
  box-shadow: 0 1px 0.5px rgb(169, 170, 171);
  border-radius: 7.5px;
  position: relative;
  z-index: 200;
}

.addUser {
  cursor: pointer;
}

.start-user {
  color: rgb(74, 104, 224);
  text-align: center;
  margin-top: 200px;
}
.msg-text{
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}